.Cart {
    
    width:100rem;
    
    margin-left:auto;
    margin-right:auto;
    background-color: white;
    margin-bottom: 20rem;

    &__title {
        font-size: 2.4rem;
        font-weight: 700;
        padding-top:2rem;
        margin-bottom:1rem;
    }
    &__totalprice {
        font-size: 3rem;
        font-weight: 600;
        padding-bottom: 4rem;
    }
    &__message {
        font-size: 2rem;
    }
    &__button {
        margin-top:2rem;
      padding: 1rem 2rem;
      font-size: 2.4rem;
      font-weight: 500;  
      margin-bottom: 4rem;
    }
}